.navBar {
  display: flex;
  padding: 1rem
}

.logo {
  align-items: center;
  display: flex;
  justify-content: end;
  margin: 0rem 3rem;
  width: 8%
}

.logo img {
  height: 3.5rem
}

.nav1 {
  display: flex;
  justify-content: end;
  padding-right: 5rem;
  width: 92%
}

.nav_link {
  color: #acabab;
  font-size: 1.19rem;
  font-weight: 500;
  margin: .5rem 2rem;
  text-decoration: none
}

.navBar a:hover {
  color: #fff;
  cursor: pointer;
  font-weight: 500
}

.h {
  display: flex;
  flex-direction: row;
  height: 32rem;
  width: 100%
}

.home {
  width: 90%
}

.home1 {
  color: #fff;
  font-family: League Spartan, Helvetica, Arial, sans-serif;
  height: 26rem;
  padding: 7rem;
  width: 100%
}

.about {
  background-color: #d55f28;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-weight: 700
}

.about:hover {
  background-color: #e76f29;
  font-weight: bolder
}

.home2 {
  color: #fff;
  font-family: League Spartan, Helvetica, Arial, sans-serif;
  height: 21rem;
  padding: 1rem;
  width: 20%
}

.home-icon {
  color: #6374f72e;
  font-size: 17rem;
  left: 35rem;
  position: absolute;
  top: 9rem
}

.text1 {
  animation: typing1 6s steps(20) infinite, cursor .5s step-end infinite alternate;
  font-size: 5rem;
  font-weight: bolder;
  margin-bottom: 0
}

.text1,
.text2 {
  overflow: hidden;
  white-space: nowrap;
  width: 100%
}

.text2 {
  animation: typing2 6s steps(40) infinite, cursor .8s step-end infinite alternate;
  font-size: 1.28rem;
  font-style: italic;
  font-weight: 700
}

@keyframes cursor {
  50% {
    border-color: #0000
  }
}

@keyframes typing1 {
  0% {
    width: 0
  }

  40% {
    width: 100%
  }

  60% {
    width: 100%
  }

  to {
    width: 0
  }
}

@keyframes typing2 {
  0% {
    width: 0
  }

  40% {
    width: 100%
  }

  60% {
    width: 100%
  }

  to {
    width: 0
  }
}

.pr,
.pr1,
.pr2,
.pr3,
.pr4,
.pr5,
.pr6,
.pr7,
.pr8 {
  position: relative
}

.pr i {
  transition: transform .5s ease
}

.pr1 i {
  color: #ff0;
  left: 7rem;
  top: 5rem
}

.pr1 i,
.pr2 i {
  position: absolute
}

.pr2 i {
  color: #c0c9d1;
  left: 54rem;
  top: 1rem
}

.pr3 i {
  color: pink;
  left: 60rem;
  top: 26rem
}

.pr3 i,
.pr4 i {
  position: absolute
}

.pr4 i {
  color: #78d9ba;
  left: 69rem;
  top: 18rem
}

.pr5 i {
  top: 14rem;
  left: 51rem
}

.pr5 i,
.pr6 i {
  color: #fff;
  position: absolute
}

.pr6 i {
  top: 26rem;
  left: 4rem
}

.pr7 i {
  top: 23rem;
  color: #76bbf9
}

.pr7 i,
.pr8 i {
  left: 31rem;
  position: absolute
}

.pr8 i {
  top: 2rem;
  color: #71dd71
}

.modal-dialog-centered {
  --bs-modal-width: 550px
}

.modal-content {
  height: 33rem;
  width: 35rem
}

.m1 {
  background-color: #e4e9e7;
  padding: .5rem;
  width: 50%
}

.modal-title {
  font-size: 2rem;
  font-weight: bolder
}

.modal-body {
  font-size: 1rem
}

.modal-body i {
  margin: .4rem 1rem
}

.m2 {
  background-color: #383444;
  color: #f5f5f5;
  padding: .5rem;
  width: 50%
}

.modal-header p {
  font-family: sans-serif;
  font-size: .9rem;
  font-style: italic
}

.m1 .modal-header {
  border-bottom: var(--bs-modal-header-border-width) solid #101010
}

.m2 .modal-header {
  border-bottom: var(--bs-modal-header-border-width) solid var(--bs-modal-header-border-color)
}

.form {
  margin-bottom: 1.08rem
}

.footer {
  background-color: #000;
  display: flex;
  justify-content: center;
  padding-bottom: 4rem;
  padding-top: 3rem;
  text-align: center
}

.f {
  color: #f5f5f5;
  font-family: sans-serif;
  font-size: 1.5rem;
  margin-right: 1.5rem
}

.footer-1 {
  align-self: center;
  display: flex;
  justify-content: center;
  text-align: center
}

.footer i {
  margin: 0 1.2rem
}

.g {
  background-color: #000;
  padding: 1rem 2rem
}

.g1 {
  background-color: #fccc36;
  height: .2rem;
  margin-bottom: .5rem;
  width: 100%
}

.g-p {
  color: #a9ccdf;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 1.1rem;
  font-style: italic;
  font-weight: 400
}

.g-p-p {
  font-size: 1rem;
  font-style: normal
}

.g a {
  color: #fcd13e;
  text-decoration: none
}

.g a:hover {
  color: #fff
}

.sidebar {
  align-self: end;
  background-color: #191524;
  height: 17rem
}

.sidebar i {
  color: #cbc7c7;
  margin: 2rem 1rem
}

.sidebar i:hover {
  color: #f8f5f5
}

.p {
  background-color: #e7e5e5;
  padding: 1rem 2rem
}

.p1 {
  background-color: #0b0b0a;
  height: .1rem;
  margin-bottom: .5rem;
  width: 100%
}

.projects-base {
  background-color: #e7e5e5
}

.projects h5 {
  color: #c59a80;
  font-weight: 700
}

.projects h1,
.projects h5 {
  font-family: sans-serif;
  margin-top: 1rem
}

.projects h1 {
  color: #545151;
  font-weight: bolder
}

.vdrive img:hover {
  opacity: 1
}

.project-item {
  display: inline-block;
  margin: 4rem;
  position: relative
}

.project-item img {
  border-radius: 1rem;
  box-shadow: 2rem 2rem 2rem #0009;
  height: 350px;
  width: 750px;
  transition: filter .3s ease
}

.project-item:hover .project-image {
  filter: blur(2px)
}

.overlay1 {
  align-items: center;
  background-color: #000c;
  border-radius: 1rem;
  color: #fff;
  display: flex;
  height: 15rem;
  justify-content: center;
  left: 40%;
  opacity: 0;
  position: absolute;
  top: 21%;
  transition: opacity .3s ease;
  width: 15rem;
  z-index: 1
}

.o1 {
  font-size: 5rem
}

.o2 {
  font-size: 2rem
}

.o1-1 {
  font-size: 3rem
}

.o1-2 {
  font-size: 1.35rem
}

.project-item:hover .overlay1 {
  opacity: 1
}

